import { Box } from '@mui/material';

export default function SmartIcon({ sx, ...other }) {
  return (
    <Box component="svg" viewBox="0 0 64 64" sx={{ width: 64, height: 64, ...sx }} {...other}>
      {/* TV Screen */}
      <rect
        x="6" y="10" width="52" height="36" rx="4"
        fill="none" stroke="#2563EB" strokeWidth="3"
      />
      {/* Stand */}
      <path d="M22 54H42" stroke="#2563EB" strokeWidth="3" strokeLinecap="round" />
      <path d="M32 46V54" stroke="#2563EB" strokeWidth="3" strokeLinecap="round" />

      {/* Signal / Wifi on screen */}
      <path d="M20 34C24 30 29 28 34 28" stroke="#2563EB" strokeWidth="3" strokeLinecap="round" fill="none" />
      <path d="M22 38C24 36 27 35 30 35" stroke="#2563EB" strokeWidth="3" strokeLinecap="round" fill="none" />

      {/* Remote Control overlay */}
      <rect
        x="44" y="24" width="14" height="36" rx="2"
        fill="#FFFFFF" stroke="#2563EB" strokeWidth="3"
      />
      <circle cx="51" cy="30" r="2" fill="#2563EB" />
      <rect x="48" y="36" width="6" height="2" fill="#2563EB" />
      <rect x="48" y="42" width="6" height="2" fill="#2563EB" />
      <rect x="48" y="48" width="6" height="2" fill="#2563EB" />
    </Box>
  );
}
