import { Box } from '@mui/material';

export default function DataCard({ sx, ...other }) {
  return (
    <Box component="svg" viewBox="0 0 64 64" sx={{ width: 64, height: 64, ...sx }} {...other}>
      {/* Sim Card Shape */}
      <path
        d="M16 8H40L52 20V56C52 58.2091 50.2091 60 48 60H16C13.7909 60 12 58.2091 12 56V12C12 9.79086 13.7909 8 16 8Z"
        fill="none" stroke="#2563EB" strokeWidth="3" strokeLinejoin="round"
      />

      {/* Chip */}
      <rect x="22" y="24" width="20" height="24" rx="2" stroke="#2563EB" strokeWidth="3" fill="none" />
      <path d="M22 36H42" stroke="#2563EB" strokeWidth="2" />
      <path d="M32 24V48" stroke="#2563EB" strokeWidth="2" />

      {/* Wifi Signal on Card */}
      <path d="M28 14C32 10 40 10 44 14" stroke="#2563EB" strokeWidth="3" strokeLinecap="round" fill="none" />
    </Box>
  );
}
