import { Box } from '@mui/material';

export default function TransferIcon({ sx, ...other }) {
  return (
    <Box component="svg" viewBox="0 0 64 64" sx={{ width: 64, height: 64, ...sx }} {...other}>
      {/* Cash Bill */}
      <rect
        x="10" y="10" width="44" height="24" rx="2"
        fill="none" stroke="#2563EB" strokeWidth="3"
      />
      <circle cx="32" cy="22" r="6" stroke="#2563EB" strokeWidth="3" />
      <path d="M14 14H18" stroke="#2563EB" strokeWidth="3" strokeLinecap="round" />
      <path d="M46 14H50" stroke="#2563EB" strokeWidth="3" strokeLinecap="round" />
      <path d="M14 30H18" stroke="#2563EB" strokeWidth="3" strokeLinecap="round" />
      <path d="M46 30H50" stroke="#2563EB" strokeWidth="3" strokeLinecap="round" />

      {/* Credit Card Overlay */}
      <rect
        x="20" y="34" width="40" height="24" rx="3"
        fill="#FFFFFF" stroke="#2563EB" strokeWidth="3"
      />
      <path d="M20 42H60" stroke="#2563EB" strokeWidth="3" />
      <rect x="26" y="48" width="12" height="4" fill="#2563EB" />
    </Box>
  );
}
