import { Box } from '@mui/material';

export default function RechargeCard({ sx, ...other }) {
  return (
    <Box component="svg" viewBox="0 0 64 64" sx={{ width: 64, height: 64, ...sx }} {...other}>
      {/* Card Shape */}
      <rect
        x="8" y="16" width="48" height="32" rx="4"
        fill="none" stroke="#2563EB" strokeWidth="3"
      />

      {/* Scratch Area */}
      <rect x="14" y="32" width="36" height="10" fill="#2563EB" rx="1" opacity="0.2" />

      {/* Logo / Chip area */}
      <circle cx="18" cy="24" r="4" fill="#2563EB" />
      <path d="M28 24H50" stroke="#2563EB" strokeWidth="3" strokeLinecap="round" />

      {/* Dashed line representing hidden numbers */}
      <path d="M16 37H48" stroke="#2563EB" strokeWidth="2" strokeDasharray="4 4" />
    </Box>
  );
}
