import { Box } from '@mui/material';

export default function BulkSmsIcon({ sx, ...other }) {
  return (
    <Box component="svg" viewBox="0 0 64 64" sx={{ width: 64, height: 64, ...sx }} {...other}>
      {/* Background shape mimicking newsletter/paper */}
      <path
        d="M10 10H46V50H10V10Z"
        fill="none" stroke="#2563EB" strokeWidth="3" strokeLinejoin="round"
      />
      {/* Fold/Shadow effect */}
      <path d="M46 10H54V50H46" stroke="#2563EB" strokeWidth="3" strokeLinejoin="round" fill="none" />
      <path d="M10 50H54V54H10V50Z" fill="#2563EB" />

      {/* Content Lines */}
      <rect x="16" y="16" width="12" height="12" stroke="#2563EB" strokeWidth="3" fill="none" />
      <path d="M32 18H42" stroke="#2563EB" strokeWidth="3" strokeLinecap="round" />
      <path d="M32 24H42" stroke="#2563EB" strokeWidth="3" strokeLinecap="round" />
      <path d="M16 34H42" stroke="#2563EB" strokeWidth="3" strokeLinecap="round" />
      <path d="M16 40H30" stroke="#2563EB" strokeWidth="3" strokeLinecap="round" />

      {/* Check/Success overlay */}
      <circle cx="44" cy="44" r="14" fill="#FFFFFF" stroke="#2563EB" strokeWidth="3" />
      <path d="M38 44L42 48L50 40" stroke="#2563EB" strokeWidth="3" strokeLinecap="round" strokeLinejoin="round" fill="none" />
    </Box>
  );
}
